body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

header {
  height: 10vh;
  background-color: #0b0c10;
  top: 0;
  left: 0;
  color: whitesmoke;
}

.main-head {
  font-family: cursive;
  text-align: center;
  margin-top: 0;
  padding-top: 1%;
  letter-spacing: 1px;
  word-spacing: 2px;
}

.main-section {
  max-width: 100vw;

}

.note {
  display: inline-table;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.068), 0 6px 5px rgba(0, 0, 0, 0.055), 0 6px 5px rgba(0, 0, 0, 0.055), 0 16px 10px rgba(0, 0, 0, 0.055);
  width: 80%;
  margin: 3%;
  background-color: whitesmoke;
  transition: all 0.3s;
  border-top: 3px solid #0b0c10;
}

.note:hover {
  box-shadow: 0 20px 20px rgba(0, 0, 0, 0.2), 5px 5px 5px rgba(0, 0, 0, 0.2);
}


.note-head {
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  margin-top: -3%;
  text-align: center;
  color: #0b0c10;
}

.note-body {
  /*font-family: cursive;*/
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  padding: 0 20px;
  margin-top: -4%;
  font-size: 15px;
  letter-spacing: 1px;
  color: #0b0c10d0;
  font-weight: 500;
  text-align: left;
}

footer {
  width: 100vw;
  bottom: 0;
  position:fixed;
}


.footer-text {
  text-align: center;
  font-size: 15px;
  letter-spacing: 0.4px;
  color: rgba(0, 0, 0, 0.712);
  font-weight: 700;
}